import React from 'react';

import { API_BASE_URL } from '../../config';

const ImageDisplay = ( { fileName } ) => {
  return (
    <div style={{ maxWidth: '750px' }}>  // Container to control max size
      <img src={`${API_BASE_URL}/play/image/${fileName}`} alt="item" style={{ width: '100%', height: 'auto' }} />
    </div>
  );
};

export default ImageDisplay;