
import { initReactI18next } from 'react-i18next';
import i18n from 'i18next';
import Backend from 'i18next-http-backend';

i18n
  .use(Backend)
  .use(initReactI18next)
  .init({
    fallbackLng: 'en',
    debug: true,
    interpolation: {
      escapeValue: false,
    },
    resources: {
      en: {
        home: require('./locales/en/home.json'),
      },
      kr: {
        home: require('./locales/kr/home.json'),
      },
    },
  });

export default i18n;