
import React from 'react';
import Typography from '@mui/material/Typography';
import { styled } from '@mui/material/styles';

import VideoPlayer from './VideoPlayer';
import ImageDisplay from './ImageDisplay';

const ResponsiveText = styled('div')({
  wordWrap: 'break-word', // Breaks the text to prevent overflow
  maxWidth: '100%',       // Ensures the text does not exceed the width of its container
});

const PhotoFrame = ( { item } ) => {

  const PhotoTitle = () => {
    return (
      <Typography variant="h4" gutterBottom>
        {item.title}
      </Typography>
    );
  };

  const PhotoLink = () => {
    return (
      <Typography variant="h4" gutterBottom>
        {item.instagram}
      </Typography>
    );
  };

  const Source = () => {
    return (
      <Typography variant="h4" gutterBottom>
        {item.source}
      </Typography>
    );
  };

  return (
    <ResponsiveText>
      <PhotoTitle />
      {item.resource && item.resource.map((fileName) => 
        fileName.endsWith('.mp4') ? 
          <VideoPlayer fileName={fileName} /> :
          <ImageDisplay fileName={fileName} />
      )}
      <PhotoLink />
      <Source />
    </ResponsiveText>
  );
};

export default PhotoFrame;