
import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { API_BASE_URL } from '../../config';
import Typography from '@mui/material/Typography';
import { TextField, Button } from '@mui/material';

const Comments = ({ itemId }) => {
  const [comments, setComments] = useState([]);

  useEffect(() => {
    const fetchComments = async () => {
      try {
        const response = await axios.get(`${API_BASE_URL}/comments/${itemId}`);
        setComments(response.data);
      } catch (error) {
        console.error('Failed to fetch comments:', error);
      }
    };

    fetchComments();
  }, [itemId]);

  return (
    <div>
      <CommentForm itemId={itemId} comments={comments} setComments={setComments} />
      {comments.length > 0 ? (
        comments.map((comment) => (
          <div key={comment.id}>
            <Typography variant="subtitle1" gutterBottom>
              {comment.author}
            </Typography>
            <Typography variant="body2" gutterBottom>
              {comment.text}
            </Typography>
          </div>
        ))
      ) : (
        <Typography variant="body1">No comments available</Typography>
      )}
    </div>
  );
};

const CommentForm = ({ itemId, comments, setComments }) => {
  const [commentText, setCommentText] = useState('');

  const handleCommentChange = (event) => {
    setCommentText(event.target.value);
  };

  const submitComment = async () => {
    try {
      const response = await axios.post(`${API_BASE_URL}/comments/${itemId}`, {
        text: commentText,
        author: 'Anonymous' // Assuming the author is anonymous for simplicity
      });
      if (response.status === 201) {
        setComments([...comments, response.data]);
        setCommentText(''); // Clear the input after successful submission
      }
    } catch (error) {
      console.error('Failed to submit comment:', error);
    }
  };

  return (
    <div>
      <TextField
        label="Write a comment"
        variant="outlined"
        fullWidth
        value={commentText}
        onChange={handleCommentChange}
        multiline
        rows={4}
      />
      <Button onClick={submitComment} variant="contained" color="primary" style={{ marginTop: '10px' }}>
        Post Comment
      </Button>
    </div>
  );
};

export default Comments;